@use "../abstracts//variables" as *;



.heading--primary{

    &--main,
    &--sub{
        text-align: center;
        text-transform:uppercase;
      
        display: block;
       

    }
    &--main{

        font-size:2.8rem;
        letter-spacing:1rem;
        font-weight: 400;
        animation: moveInLeft 1s linear;
    }

    &--sub{
        font-size: 1.6rem;
        letter-spacing: 1.2rem;
        font-weight: 700;
        animation: moveInRight 1s linear;
    }
}

.heading--secondary{
    font-size: 2.4rem;
    text-transform: capitalize;
    font-weight: 700;
    display: inline-block;
    margin-inline: auto;
    background-image:linear-gradient(to right ,$clr-primary-tint,$clr-primary-shade);
    color:transparent;
    -webkit-background-clip: text;
    transition: all 0.6s;

   
&:hover{
    transform: scaleX(1.1) skewX(10deg);
}

}

.heading--tertiary{
    font-size: 1.8rem;
    text-transform: capitalize;
    letter-spacing: 0.1rem;
}

.paragraph{
    font-size: 1.4rem;

    &:not(:last-child){
        margin-bottom: 3rem;
    }
}