@use "../abstracts" as *;
.story{

    width:80%;
    max-width: 60rem;
    padding:1rem 4rem;
    box-shadow: rgba(0, 0, 0, 0.54) 0px 3px 8px;
 
transform: skewX(-5deg);

 background-color: $clr-white;

& > *{
    transform: skewX(5deg);
}

 &__shape{
    width:15rem;
    height: 15rem;
    float:left;
  
   
  shape-outside: circle(50% at 50% 50%);
  clip-path: circle(50% at 50% 50%);
  transform: translateX(-2rem) skewX(5deg);
  position: relative;
 }

 &__img{
 height: 100%;
 transform: scale(1.3);
 transition: all 0.5s;
 }

 &__caption{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,50%);
    color:$clr-white;
    text-transform: uppercase;
    font-size: 1.6rem;
    text-align: center;

    opacity: 0;
    transition: all 0.5s;
    backface-visibility: hidden;
    
  

 }
 &:hover &__caption{
    transform:translate(-50% ,-50%);
    opacity: 1;
 }
 &:hover &__img{
    filter:blur(4px) brightness(50%);

    transform: scale(1);
 }
}